ul.nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
    top: 0;
    height: 100%;
    width: 100%;
    font-weight: bold;
}

li.nav {
    float: left;
}

li.nav button.nav {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
    outline:none;
}

li.nav button.nav:hover:not(.active) {
    background-color: #111;
}

.active {
    background-color: #4CAF50;
}
li.nav button.nav:not(.active)
{
    background-color: Transparent;
}

li.pushedRight {
    float: right;
}