.aboutContainer{
    background-color: #111;
    width: 100%;
    height: 100%;
    grid-row: 2/3;
    grid-column: 1/6;
    
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.info{
    grid-column: 1/2;
    animation: infoAnimation 2s linear 1 alternate;
    animation-fill-mode: forwards;
    overflow:auto;
    
    
    
}

.all{
    display:inline-block;
    color:black;
    font-weight: 900;
    
    border-radius: 30px;
    padding: 5px;
    margin: 5px;
    text-align: center;
}

@keyframes infoAnimation {
    from {background-color: transparent;opacity: 0;}
    to {background-color: blue;opacity: 1;}
  }

  .info-detail{
    display:inline-block; 
      background-color: cornflowerblue;
      font-size: small;
    
    transition: transform 0.5s;
  }

  .info-detail:hover{
      transform: scale(1.25,1.25);
  }

  .info-header{
      background-color: darkcyan;
      width:95%;
      font-size: medium;
      
  }

  .detail-wrapper{
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around   ;
    height: 80%;
  }