body {
    font-family: 'Alatsi';
    font-size: 22px;
    background-color: rgb(192, 189, 189);
    overflow: hidden;
}

.layoutWrapper {
    display: grid;
    grid-template-rows: min(60px) 1fr min(60px);
    color: #444;
    height: 100vh;
}

.layoutWrapper>div {
    width: 100%;
    height: 100%;
}

.header {
    grid-column: 1/6;
    grid-row: 1/2;
}

.content {
    grid-row: 2/3;
    grid-column: 1/6;
    background-color: #111;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    overflow-y: auto;
    border-radius: 5px;
}

.footer {
    grid-column: 1/6;
    grid-row: 3/4;
    background-color: #333;
    color:white;
    text-align: center;
    padding: 14px 16px;
    font-weight: bold;
    font-size: 13px;
}