.middle {
    grid-column: 2/5;
    grid-row: 1/2;
    margin-top: 10px;
    overflow: auto;
    padding: 5px;
}

.side {
    grid-column: 1/2;
    grid-row: 1/2;
    margin-top: 10px;
    overflow-y: auto;
    font-size: medium;
    font-weight: bold;
}

.side ul {
    list-style-type: none;
    padding-left: 0;
    margin-left: 2%;
}

.side li,
.side a {
    display: block;
    color: rgb(176, 202, 181);
    text-decoration: none;
}

.side li:hover:not(.active) {
    background-color: #1d2220
}

.side .active {
    background-color: #4CAF50;
}